body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  transition: color .3s, transform .3s;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link:hover {
  cursor: pointer;
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin: auto;
  position: absolute;
  bottom: 0;
  left: 0;
}

li:not(:first-child) {
  margin-left: 20px;
}

.card {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  height: 406px;
  padding-left: 16px;
  padding-right: 16px;
  font-family: Roboto, sans-serif;
  transition: color .3s, background-color .3s, transform .3s;
  display: flex;
}

.card__image {
  justify-content: center;
  padding-top: 32px;
  display: flex;
}

.card__imac {
  object-fit: contain;
  max-width: 160px;
  height: 134px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  justify-content: flex-start;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  margin-top: -15px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin: 25px 0 0;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin-right: 85px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: auto;
  margin-bottom: 17px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s, transform .3s;
  display: flex;
}

.card__buy:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: flex-start;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-top: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}

.catalog {
  justify-content: center;
  gap: 46px;
  padding: 50px 40px;
  display: grid;
}

.catalog__block {
  grid-template-columns: repeat(1, 200px);
  gap: 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__block {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__block {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__block {
    grid-template-columns: repeat(4, 200px);
  }
}
